import { Chess } from 'chess.js';

export function createNewGame() {
    return new Chess();
}

// Adaptation pour prendre explicitement des arguments `from` et `to`
export function handleMove(game, from, to,setGameStatus) {
   
    try
    {
       const gameCopy = new Chess(game.fen());  // Crer une copie du jeu
       
      const move = gameCopy.move({
        from,
        to,
        promotion: 'q'  // Promotion automatique pour simplifier
      });
      
      if (gameCopy.turn() === 'b') {
        setGameStatus('En cours : Au noir de jouer');
      } else {
        setGameStatus('En cours : Au blanc de jouer');
      }

     
    
      if (move) {
        // Dtection de l'tat de la partie aprs le mouvement
        let status;
        if (gameCopy.isCheckmate()) {
          if(move.color ==='w')
            setGameStatus('chec et mat : Victoire des blancs');
          else
          setGameStatus ("chec et mat : Vctoire des noirs")
        } else if (gameCopy.isStalemate()) {
          setGameStatus('Patte (galit');
        } else if (gameCopy.isCheck()) {
          if(move.color ==='w')
            setGameStatus ("Roi noir en chec : Dplacement limits, contrer de l'chec");
          else
          setGameStatus ("Roi blanc en chec : Dplacement limits, contrer de l'chec");
            
        } 
        
        // Retourner  la fois le mouvement et le statut
        const valueMove = move ? gameCopy: null
        return { game: gameCopy, valueMove, status ,move};
    }
       
    }
    catch(error)
    {
      console.error('Erreur lors du dplacement:', error);
      return null;
    }
  }
  
